import { useEffect, useState } from 'react';
import {
	Table,
	Image,
	Button,
	Space,
	Form,
	Divider,
	Modal,
	Input,
	Upload
} from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import api from '../../assets/api/api';
import url from '../../assets/api/url';
import './index.scss';

const { Column } = Table;

const Modules = () => {
	const [form] = Form.useForm();
	const [dataList, setDataList] = useState([]);
	const [isShow, setShow] = useState(false);
	const [param, setParam] = useState({});
	const [fileList, setFileList] = useState([]);
	const [size, setSize] = useState(10);
	const [current, setCurrent] = useState(1);
	const [total, setTotal] = useState(0);
	const initData = async () => {
		const { data } = await api.get(url.modules, { current, size });
		setDataList(data?.list?.map((item, key) => {
			return {
				key: key + 1,
				name: item.name,
				src: item.src,
				id: item.id,
				tip: item.tip
			};
		}));
		setTotal(data.total);
	};
	useEffect(() => {
		form.resetFields();
	}, [isShow]);
	useEffect(async () => {
		initData();
	}, [current, size]);
	const onAddClick = (value) => {
		setShow(true);
		if (value.id) {
			setParam({ ...param, ...value });
			setFileList([
				{
					url: value.src
				}
			]);
			return;
		}
		setParam({});
		setFileList([]);
	};
	const onDelectClick = async (value) => {
		Modal.confirm({
			content: '',
			okText: '',
			cancelText: '',
			onOk: async () => {
				await api.delete(`${url.modules}/${value.id}`);
				initData();
			}
		});
	};
	const normFile = (e) => {
		if (Array.isArray(e)) {
			return e;
		}
		return e?.file?.response || '';
	};
	const handleSubmit = async (value) => {
		if (param.id) {
			value.id = param.id;
		}
		await api[param.id ? 'put' : 'post'](url.modules, value);
		setParam({});
		setShow(false);
		initData();
	};
	const handleCancel = () => {
		setShow(false);
		setParam({});
	};
	return (
		<>
			<Form layout="inline" form={form}>
				<Form.Item>
					<Button type="primary" htmlType="button" onClick={onAddClick}></Button>
				</Form.Item>
			</Form>
			<Divider />
			<Table
				bordered
				dataSource={dataList}
				pagination={{
					pageSize: size,
					current,
					total,
					showSizeChanger: true,
					showTotal: () => {
						return `${total}`;
					},
					onChange: (page) => {
						setCurrent(page);
					},
					onShowSizeChange: (_, pageSize) => {
						setSize(pageSize);
					}
				}}
			>
				<Column title="" dataIndex="key" key="key" align="center" />
				<Column title="" dataIndex="name" key="name" align="center" />
				<Column title="" dataIndex="src" key="src" align="center" render={(_, record) => <Image width={100} src={record.src} />} />
				<Column title="" dataIndex="tip" key="tip" align="center" />
				<Column
					title=""
					dataIndex="action"
					key="action"
					align="center"
					render={(_, record) => (
						<Space>
							<Button type="primary" size="small" onClick={() => onAddClick(record)}></Button>
							<Button type="primary" size="small" danger ghost onClick={() => onDelectClick(record)}></Button>
						</Space>
					)}
				/>
			</Table>
			<Modal
				title={param.id ? '' : ''}
				footer={null}
				getContainer={false}
				destroyOnClose
				visible={isShow}
				onCancel={handleCancel}
				maskClosable={false}
			>
				<Form
					form={form}
					preserve={false}
					layout="horizontal"
					initialValues={param}
					onFinish={handleSubmit}
				>
					<Form.Item
						label=""
						name="name"
						rules={[
							{
								required: true,
								message: '!'
							}
						]}
					>
						<Input />
					</Form.Item>
					<Form.Item
						label=""
						name="src"
						rules={[
							{
								required: true,
								message: '!'
							}
						]}
						valuePropName="src"
						getValueFromEvent={normFile}
					>
						<Upload
							defaultFileList={fileList}
							onRemove={() => {
								setParam({ ...param, ...{ src: '' } });
							}}
							customRequest={async (options) => {
								const { onSuccess, file } = options;
								const { data } = await api.upload(url.upload, file);
								onSuccess(data);
							}}
							listType="picture"
						>
							{
								param.src ? null : <Button icon={<UploadOutlined />}></Button>
							}
						</Upload>
					</Form.Item>
					<Form.Item
						label=""
						name="tip"
					>
						<Input />
					</Form.Item>
					<Form.Item>
						<Space>
							<Button type="primary" htmlType="submit"></Button>
							<Button type="default" htmlType="button" onClick={handleCancel}></Button>
						</Space>
					</Form.Item>
				</Form>
			</Modal>
		</>
	);
};

export default Modules;
